import React from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import axios from 'axios'
import config from '../../../utils/config'

import { setRouterLocationAction } from '../../../flux/actions/authActions'

const HOST = config.HOST


class Address extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      address: [],
      message: ''
    }
  }
  componentWillMount() {
    this.props.setRouterLocationAction('/member/address')
    if (this.props.memberId === -1) return;
    // 
    axios.post(HOST + '/api/users/address', {memberId: this.props.memberId})
      .then(res => {
        if (res.data.success) {
          this.setState({
            address: res.data.payload,
            message: ''
          })
        }else {
          this.setState({
            address: [],
            message: res.data.message
          })
        }
      })
      .catch(err => {
        this.setState({
          address: [],
          message: err.message
        })
      })
  }
  render() {
    return (
      <div>
        <div></div>
        <div><span></span></div>
        <div>
          <span>:</span>
          <input type="text"/>
        </div>
        <div>
          <span>:</span>
          <input type="text"/>
        </div>
        <div>
          <span>:</span>
          <input type="text"/>
        </div>
        <div>
          <span>:</span>
          <input type="text"/>
        </div>
        <div>
          <input type="checkbox"/>
          <span></span>
        </div>
        <button></button>
        <div>
          <table>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th>/</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
            {
              this.state.address.map((item, index) => (
                <tr key={index}>
                  <td>{item.receiveName}</td>
                  <td>{item.address}</td>
                  <td>{item.postcode}</td>
                  <td>{item.phone}</td>
                  <td>/</td>
                </tr>
              ))
            }
            </tbody>
          </table>
        </div>
      </div>
    )
  }
}
function mapStateToProps(state) {
  return {
    address: state.auth.address,
    memberId: state.auth.user._id === undefined ? -1 : state.auth.user._id
  }
}
Address.propTypes = {
  address: PropTypes.array.isRequired,
  memberId: PropTypes.number.isRequired
}
export default connect(mapStateToProps, { setRouterLocationAction })(Address)
